import { mapState } from 'vuex'
export default {
    name:"aimate_getmoney",
    created(){
    },
    computed:{
        ...mapState({
            getMoney: state => state.activityModule.getMoney
        })
    },
    watch:{
        getMoney(newval,oldval) {
            this.$refs.audio.play();
        }
    },
    mounted() {
        this.$refs.audio.play();
    }
}



